.stake-container {
  max-width: 900px;
  margin: 0 auto;
  padding: 20px;
  font-family: Arial, sans-serif;
  color: #333;
}

.stake-title {
  text-align: center;
  font-size: 32px;
  font-weight: bold;
  margin-bottom: 30px;
}

/* 卡片通用样式 */
.card {
  background: #fff;
  padding: 20px;
  border-radius: 16px;
  box-shadow: 0 8px 16px rgba(0,0,0,0.1);
  margin-bottom: 20px;
}

/* RewardPoolInfo 特殊渐变背景 */
.reward-pool {
  background: linear-gradient(90deg, #a0e9ff, #72c7ff);
  color: #0b3d91;
}

/* 用户收益 */
.pending-reward {
  font-size: 18px;
  margin-bottom: 12px;
}

/* 按钮 */
.action-buttons {
  display: flex;
  gap: 12px;
  flex-wrap: wrap;
}

.btn {
  flex: 1;
  padding: 10px 0;
  border: none;
  border-radius: 12px;
  font-weight: bold;
  cursor: pointer;
  color: #fff;
  transition: all 0.3s ease;
}

.btn.deposit { background: #28a745; }
.btn.deposit:hover { background: #218838; }

.btn.withdraw { background: #dc3545; }
.btn.withdraw:hover { background: #c82333; }

.btn.claim { background: #007bff; }
.btn.claim:hover { background: #0069d9; }

/* 表格样式 */
.table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 10px;
}

.table th, .table td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}

.table th {
  background: #f5f5f5;
  font-weight: bold;
}

.table tr:hover {
  background: #f1f1f1;
}

.empty-text {
  color: #888;
  font-style: italic;
}
